<script>
import $ from 'jquery';

export default {
  props: ['all_products'],
  data() {
    return {
      banner_link: String,
      categoryNav: Array,
      carousel: Array,
      background_interval: null,
      header: {
        leftArrow_show: false,
        rightArrow_show: false,
      },
      category: {
        leftArrow_show: false,
        rightArrow_show: false,
      },
      horizontal_scroll: true,
    };
  },
  methods: {
    change_bgimg(whichImg) {
      const vm = this;
      vm.carousel.forEach((element) => {
        vm.$set(element, 'is_shown', false);
      });
      vm.carousel[whichImg].is_shown = true;
      vm.banner_link = `front/product/${vm.carousel[whichImg].id}`;
    },
    var_interval() {
      const vm = this;
      vm.background_interval = setInterval(() => {
        let nextImg;
        vm.carousel.forEach((element, index) => {
          if (element.is_shown === true) {
            if (index === vm.carousel.length - 1) {
              // 如果輪播到最後一張圖片
              nextImg = 0;
            } else {
              nextImg = index + 1;
            }
          }
        });
        vm.change_bgimg(nextImg);
      }, 8000);
    },
    start_interval() {
      this.var_interval();
    },
    end_interval() {
      clearInterval(this.background_interval);
    },
    initialized_page() {
      const vm = this;
      let tempObj = vm.all_products.filter(
        (item) => item.on_banner === true,
      );
      vm.carousel = JSON.parse(JSON.stringify(tempObj));
      vm.change_bgimg(0);
      vm.start_interval();

      // categoryNav------
      // 將所有商品的所有類別都push到tempArr，再用filter篩選不重複的類別，再傳入vm.categoryNav
      tempObj = vm.all_products.map((element) => element.category);
      const tempArr = [];
      tempObj.forEach((element) => {
        element.forEach((el) => {
          tempArr.push(el);
        });
      });
      const categoryList = tempArr.filter((element, index, arr) => arr.indexOf(element) === index);
      vm.categoryNav = JSON.parse(JSON.stringify(categoryList));
      // ------

      vm.trigger_arrow(this.header, 'header__nav');
      vm.trigger_arrow(this.category, 'category__list');
      $(window).resize(() => {
        vm.trigger_arrow(this.header, 'header__nav');
        vm.trigger_arrow(this.category, 'category__list');
      });
    },
    trigger_background_switch(whichImg) {
      this.end_interval();
      this.change_bgimg(whichImg);
      this.start_interval();
    },
    trigger_arrow(targetObj, targetStr) {
      const parent = document.querySelector(`.${targetStr}`);
      const parentWidth = $(parent).width();
      const children = document.querySelectorAll(`.${targetStr}__item`);
      const parentScrollLeft = $(parent).scrollLeft();
      let childrenWidth = 0;
      children.forEach((child) => {
        childrenWidth += $(child).outerWidth();
      });
      if (childrenWidth > parentWidth) {
        if (parentScrollLeft > 0) {
          this.$set(targetObj, 'leftArrow_show', true);
        } else {
          this.$set(targetObj, 'leftArrow_show', false);
        }
        if (parentScrollLeft + parentWidth + 1 < childrenWidth) {
          this.$set(targetObj, 'rightArrow_show', true);
        } else {
          this.$set(targetObj, 'rightArrow_show', false);
        }
      } else {
        this.$set(targetObj, 'leftArrow_show', false);
        this.$set(targetObj, 'rightArrow_show', false);
      }
    },
    become_horizontal_scrollable(targetObj, targetStr) {
      const vm = this;
      const element = document.querySelector(`.${targetStr}`);
      if (targetObj.leftArrow_show === true
      || targetObj.rightArrow_show === true) {
        element.addEventListener('wheel', (e) => {
          e.preventDefault();
          vm.scroll_horizontally(targetObj, targetStr, e.deltaY);
        });
      }
    },
    scroll_horizontally(targetObj, targetStr, direction) {
      const scrollElement = document.querySelector(`.${targetStr}`);
      if (this.horizontal_scroll === true) {
        this.horizontal_scroll = false;
        if (direction > 0) {
          // console.log('往右');
          $(scrollElement).animate({
            scrollLeft: '+=60px',
          }, 'fast');
          // scrollElement.scrollLeft += 30;
          this.trigger_arrow(targetObj, targetStr);
        } else {
          // console.log('往左');
          $(scrollElement).animate({
            scrollLeft: '-=60px',
          }, 'fast');
          // scrollElement.scrollLeft -= 30;
          this.trigger_arrow(targetObj, targetStr);
        }
        setTimeout(() => {
          this.horizontal_scroll = true;
        }, 50);
      }
    },
    trigger_menu() {
      const folded = document.querySelector('.hamburger--folded');
      const cross = document.querySelector('.hamburger--cross');
      const category = document.querySelector('.category');
      const foldedDisplay = $(folded).css('display');
      const categoryList = document.querySelectorAll('.category__list');
      if (foldedDisplay === 'block') {
        $(folded).css('display', 'none');
        $(cross).css('display', 'block');
        $(category).css('display', 'flex');
        $(categoryList).animate({
          marginLeft: '0px',
          opacity: 1,
        }, 500);
        this.trigger_arrow(this.category, 'category__list');
      } else {
        $(folded).css('display', 'block');
        $(cross).css('display', 'none');
        $(category).css('display', 'none');
        $(categoryList).animate({
          marginLeft: '50px',
          opacity: 0,
        });
      }
    },
    goPage(page) {
      this.$router.push(`/${page}`);
    },
    test() {
      this.$emit('emit_user_category', '123');
      this.$router.push('/front/onlinestore');
    },

  },
  mounted() {
    this.initialized_page();
  },
};
</script>
